import share from '@/components/share';
import {
	myrequest,
	detailurl,
	collectionisurl,
	collectionaddurl,
	collectionreduceurl,
	defaultaddrdetailurl,
	orderaddurl,
	myget,
	myshowModal
} from '../../api.js';
export default {
	components: {
		share
	},
	data() {
		return {
			favorite: false,
			flist: [],
			uid: "",
			sid: "",
		};
	},
	async onLoad(options) {
		//接收传值,id里面放的是标题，因为测试数据并没写id 
		let detail = await myrequest(detailurl, 'GET', {
			id: options.id
		});
		this.flist = detail["data"];
		this.sid = this.flist.id;
		this.uid = await myget('uid');
		let res = await myrequest(collectionisurl, 'POST', {
			uid: this.uid,
			sid: this.sid,
		});
		console.log(res["respCode"]);
		if (res["respCode"] === 0) {
			this.favorite = true;
		}
	},
	methods: {
		//收藏
		toFavorite() {
			this.favorite = !this.favorite;
			if (this.favorite) {
				myrequest(collectionaddurl, 'POST', {
					uid: this.uid,
					sid: this.sid
				});
			} else {
				myrequest(collectionreduceurl, 'POST', {
					uid: this.uid,
					sid: this.sid
				});
			}
		},
		async buy(options) {
			const data = await myrequest(defaultaddrdetailurl, 'POST', {
				uid: this.uid
			});
			if (data.respCode === 1) {
				uni.showToast({
					title: '请设置默认地址',
					icon: 'none',
				});
				return;
			}
			const res = await myshowModal();
			if(res){
				const sendData = {
					sid: this.flist.id,
					uid: this.uid,
					address: data.data.address.address,
					area: data.data.address.area,
					name: data.data.address.name,
					mobile: data.data.address.mobile,
				};
				const res = await myrequest(orderaddurl, 'POST', sendData);
				uni.switchTab({
					url: `/pages/order/order`
				});
			}else{
				return;
			}
		},
	},

}
